
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



body,
html {
  padding: 0;
  margin: 0;
  height: 100%;
}

*,
*:after,
*:before {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

 
body {
  margin: 0;
  color: #f2ede3;
  background: #333;
  font-size: 0.8em;

  font-family: "Lato", "Calibri", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



a {
  color: #f0f0f0;
  /* background-color: #636363; */
  text-decoration: underline;
  /* font-weight: 700; */
  /* letter-spacing: 2px; */
  /* padding: 0 5px; */
  /* text-transform: uppercase; */
  /* font-size: 80%; */
}

a:hover {
  color: #fff;
}

.container {
  height: 100%;
}

.bl-main {
  position: absolute;
  width: 100%;
  /* height: 100%; */
  overflow: hidden;
  bottom: 0px;
  top: 0px;
  overflow: hidden;
}

.bl-main-dropdown {
  top: 50px;
}

.nav {
  /* height: 4%; */

  padding: 10px 0 0 20px;
}

.nav-x-info {
  right: 0;
  top: 0;
  position: absolute;
}

.nes {
  font-family: 'Press Start 2P';
  font-weight: 100;
}

.nes-container {
  margin-bottom: 20px;
}

.nes label {
  font-size: 16px;
  margin: 0 0 30px 0;
}
.nav .nav-close {
  z-index: 100000;
  position: fixed;
  right: 20px;
  bottom: 10px;
}

.nav .nav-question {
  z-index: 100000;
  position: fixed;
  top: 6px;
  right: 6px;
}

.nav .nav-edit {
  z-index: 100000;
  position: fixed;
  right: 95px;
  bottom: 10px;
}

.loading {
  display: inline-block;
  width: 60px;
  height: 60px;
  border: 6px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: #fff;
  animation: spin 1s ease-in-out infinite;
  clear: both;
  -webkit-animation: spin 1s ease-in-out infinite;
}

.match {
  font-weight: bold;
}

@keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

@-webkit-keyframes spin {
  to {
    -webkit-transform: rotate(360deg);
  }
}

.nav .nav-icon {
  cursor: pointer;
}
.bl-main > section {
  position: absolute;
  width: 50%;
  height: 50%;
}

.bl-main > section:first-child {
  top: 0;
  left: 0;
}

.bl-main > section:nth-child(2) {
  top: 0;
  left: 50%;
}

.bl-main > section:nth-child(3) {
  top: 50%;
  left: 0;
}

.bl-main > section:nth-child(4) {
  top: 50%;
  left: 50%;
}

.bl-box {
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  opacity: 1;
  /* Centering with flexbox */
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-pack: center;
  -moz-box-pack: center;
  -webkit-justify-content: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -moz-box-align: center;
  -webkit-align-items: center;
  -ms-flex-align: center;
  align-items: center;
}

.bl-box h2 {
  text-align: center;
  margin: 0;
  /* padding: 20px; */
  width: 100%;
  letter-spacing: 2px;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 1.4em;
  line-height: 1.3;
}

.bl-box h4 {
  text-align: center;
  margin: 0;
  /* padding: 20px; */
  width: 100%;
  letter-spacing: 2px;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 1.0em;
}

.freeze .bl-box:first-child {
  animation-duration: 0s;
}

.bl-icon {
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  cursor: pointer;
  -webkit-font-smoothing: antialiased;
}

.bl-icon:before {
  display: block;
  font-size: 2em;
  margin-bottom: 10px;
}

.bl-icon-about:before {
  content: "\e000";
}

.bl-icon-works:before {
  content: "\e001";
}

.bl-icon-blog:before {
  content: "\e002";
}

.bl-icon-contact:before {
  content: "\e003";
}

.bl-main > section .bl-icon-close {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  z-index: 100;
  opacity: 0;
  pointer-events: none;
  background-color: #636363;
}

.bl-icon-close:before {
  content: "\e005";
}

.bl-content,
div.bl-panel-items > div > div {
  opacity: 0;
  pointer-events: none;
  position: absolute;
  width: 100%;
  top: 10px;
  /* left: 20px; */
  /* right: 20px; */
  bottom: 0px;
  padding: 0 20px 0 20px;
  overflow: hidden;
  overflow-y: scroll;
}

/* Custom content */

.bl-content p,
.bl-content .paragraph {
  margin: 0 auto;
  padding-bottom: 15px;
  font-size: 1.7em;
  line-height: 1.4;
}

.bl-content h2 {
  font-size: 3em;
  font-weight: 300;
  margin: 0 0 20px 0;
}

.bl-content article {
  padding: 20px 40px 20px 0px;
}

.bl-content article h3 {
  font-weight: 700;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 0 0 10px 0;
  padding-top: 20px;
  font-size: 1.4em;
}

.bl-content article a {
  color: rgba(0, 0, 0, 0.2);
}

.bl-content > ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.bl-content > ul li {
  display: inline-block;
  width: 20%;
  margin: 1%;
}

.bl-content > ul li a {
  display: block;
  padding: 0;
  border: 8px solid rgba(0, 0, 0, 0.1);
}

.bl-content > ul li a img {
  display: block;
  max-width: 100%;
}

/* Panel Items */

div.bl-panel-items,
div.bl-panel-items > div {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  position: absolute;
}

div.bl-panel-items > div > div {
  width: 60%;
  margin: 0 auto;
  opacity: 1;
  bottom: 90px;
  top: 90px;
  pointer-events: auto;
}

div.bl-panel-items > div > div h3 {
  font-size: 2.4em;
  font-weight: 300;
  margin: 0 0 20px 0;
}

div.bl-panel-items > div > div p {
  font-size: 1.3em;
}

div.bl-panel-items > div > div img {
  float: left;
  margin: 0 20px 20px 0;
  max-width: 100%;
}

div.bl-panel-items {
  top: 100%;
  z-index: 9999;
}

div.bl-panel-items > div {
  /* background: #3ba5db; */
  z-index: 0;
  opacity: 0;
  -webkit-transform: translateY(0);
  -webkit-transition: -webkit-transform 0.5s ease-in-out, opacity 0s linear 0.5s;
  -moz-transform: translateY(0);
  -moz-transition: -moz-transform 0.5s ease-in-out, opacity 0s linear 0.5s;
  transform: translateY(0);
  transition: transform 0.5s ease-in-out, opacity 0s linear 0.5s;
  -ms-transform: translateY(0);
}

div.bl-panel-items nav {
  position: absolute;
  z-index: 9999;
  width: 216px;
  left: 50%;
  top: 0px;
  margin-left: -108px;
  opacity: 0;
  -webkit-transition: opacity 0.2s ease-in-out 0.5s;
  -moz-transition: opacity 0.2s ease-in-out 0.5s;
  transition: opacity 0.2s ease-in-out 0.5s;
}

div.bl-panel-items.bl-panel-items-show nav span {
  float: left;
  margin: 5px;
}

div.bl-panel-items nav span.bl-next-work {
  font-weight: 700;
  letter-spacing: 2px;
  display: block;
  text-transform: uppercase;
  line-height: 2em;
  cursor: pointer;
  margin-right: 2em;
}

div.bl-panel-items.bl-panel-items-show nav {
  opacity: 1;
  top: -70px;
}

div.bl-panel-items > div.bl-show-work {
  z-index: 1000;
  opacity: 1;
  -webkit-transform: translateY(-100%);
  -webkit-transition: -webkit-transform 0.5s ease-in-out;
  -moz-transform: translateY(-100%);
  -moz-transition: -moz-transform 0.5s ease-in-out;
  transform: translateY(-100%);
  transition: transform 0.5s ease-in-out;
  -ms-transform: translateY(-100%);
}

div.bl-panel-items > div.bl-hide-current-work {
  opacity: 0;
  -webkit-transition: -webkit-transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  -webkit-transform: translateY(-100%) scale(0.5);
  -moz-transition: -moz-transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  -moz-transform: translateY(-100%) scale(0.5);
  transition: transform 0.5s ease-in-out, opacity 0.5s ease-in-out;
  transform: translateY(-100%) scale(0.5);
  -ms-transform: translateY(-100%) scale(0.5);
  z-index: 0;
}

/* Transition classes and properties */
/* Separated for a better overview and control */

.bl-main > section {
  -webkit-transition: all 0.5s ease-in-out;
  -moz-transition: all 0.5s ease-in-out;
  transition: all 0.5s ease-in-out;
}

.bl-main > section.bl-expand {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.bl-main > section.bl-expand-top {
  z-index: 100;
}

/* .bl-main > section:first-child.bl-expand {
  background: #ee4444;
}
.bl-main > section:nth-child(2).bl-expand {
  background: #f98262;
}
.bl-main > section:nth-child(3).bl-expand {
  background: #4bbe8e;
}
.bl-main > section:nth-child(4).bl-expand {
  background: #0d8278;
} */

.bl-main.bl-expand-item > section:not(.bl-expand),
.bl-main.bl-expand-item > section.bl-scale-down {
  -webkit-transform: scale(0.5);
  -moz-transform: scale(0.5);
  -ms-transform: scale(0.5);
  transform: scale(0.5);
  opacity: 0;
}

.bl-box {
  -webkit-transition: opacity 0.2s linear 0.5s;
  -moz-transition: opacity 0.2s linear 0.5s;
  transition: opacity 0.2s linear 0.5s;
}

section.bl-expand .bl-box {
  opacity: 0;
  -webkit-transition: opacity 0s linear;
  -moz-transition: opacity 0s linear;
  transition: opacity 0s linear;
}

.bl-box h2 {
  -webkit-transition: all 0.2s ease-in-out;
  -moz-transition: all 0.2s ease-in-out;
  transition: all 0.2s ease-in-out;
}

/* section:not(.bl-expand) .bl-box:hover h2 {
  -webkit-transform: translateY(-15px);
  -moz-transform: translateY(-15px);
  -ms-transform: translateY(-15px);
  transform: translateY(-15px);
} */

.bl-content,
.bl-icon-close {
  -webkit-transition: opacity 0.1s linear 0s;
  -moz-transition: opacity 0.1s linear 0s;
  transition: opacity 0.1s linear 0s;
}

section.bl-expand .bl-content,
section.bl-expand .bl-icon-close {
  pointer-events: auto;
  opacity: 1;
  -webkit-transition: opacity 0.3s linear 0.5s;
  -moz-transition: opacity 0.3s linear 0.5s;
  transition: opacity 0.3s linear 0.5s;
}

@media screen and (max-width: 46.5em) {
  .bl-content,
  .bl-box {
    font-size: 75%;
  }

  .bl-expand .bl-box {
    height: 130px;
  }

  .bl-content > ul li {
    width: 40%;
  }
}
